<script setup lang="ts">
	import { NoticeItem } from '../../../types/notice'
	import { ref } from 'vue'

	//高亮
	const activeIndex = ref(0)
	const onChange = (ev) => {
		activeIndex.value = ev.detail!.current
	}

	//接收父组件传递的数据
	defineProps<{
		list : NoticeItem[]
	}>()
</script>

<template>
	<!-- 公告轮换 -->
	<view class="carousel">
		<swiper :circular="true" :autoplay="true" :interval="3000" @change="onChange">
			<swiper-item v-for="item in list" :key="item._id">
				<navigator open-type="navigate" :url="`/pages/notice/notice?id=${item._id}`" hover-class="none"
					class="navigator">
					<image mode="aspectFill" class="image" :src="item.imgUrl"></image>
				</navigator>
			</swiper-item>
		</swiper>
		<!-- 指示点 -->
		<view class="indicator">
			<text v-for="(item, index) in list" :key="item._id" class="dot"
				:class="{ active: index === activeIndex }"></text>
		</view>
	</view>
</template>

<style lang="scss">
	/* 轮播图 */
	.carousel {
		height: 280rpx;
		position: relative;
		overflow: hidden;
		transform: translateY(0);
		background-color: #efefef;

		.indicator {
			position: absolute;
			left: 0;
			right: 0;
			bottom: 16rpx;
			display: flex;
			justify-content: center;

			.dot {
				width: 30rpx;
				height: 6rpx;
				margin: 0 8rpx;
				border-radius: 6rpx;
				background-color: rgba(255, 255, 255, 0.4);
			}

			.active {
				background-color: #fff;
			}
		}

